import React, { FunctionComponent } from "react";
import { Prompt } from "./Prompt";

export type HistoryItem = {
  prompt: string;
  output?: FunctionComponent;
}

export const HistoryItem: FunctionComponent<HistoryItem> = ({ prompt, output }) => {
  // See: https://reactjs.org/docs/jsx-in-depth.html#user-defined-components-must-be-capitalized
  const OutputComponent = output;
  return (
    <li>
      <Prompt active={false} value={prompt} />
      {OutputComponent && <OutputComponent />}
    </li>
  );
};
