import React, { FunctionComponent, useState } from "react";
import { Prompt } from "./Prompt";
import { History } from "./History";
import { HistoryItem } from "./HistoryItem";
import commands, { Command } from "../commands";

export const Terminal: FunctionComponent = () => {
  const availableCommands: Record<string, Command> = commands;
  const initialHistory: History = [];
  const [history, setHistory] = useState(initialHistory);
  const pushToHistory = (historyItem: HistoryItem) => setHistory(oldHistory => [...oldHistory, historyItem]);

  const executePrompt = (prompt: string) => {
    if (!prompt) {
      pushToHistory({
        prompt,
      });

      return;
    }

    const command = availableCommands[prompt];
    if (!command) {
      pushToHistory({
        prompt,
        output: () => <div>command not found: {prompt}</div>,
      });

      return;
    }

    pushToHistory({
      prompt,
      output: command.component,
    });
  };

  return (
    <div id="terminal">
      <History history={history} />
      <Prompt onOk={executePrompt} />
    </div>
  );
};
