import React, { FormEvent, FunctionComponent, useEffect, useRef, useState } from "react";

type Config = {
  active?: boolean,
  value?: string
  onOk?: (command: string) => void
}

export const Prompt: FunctionComponent<Config> = ({ active = true, value, onOk }: Config) => {
  const [prompt, setPrompt] = useState(value || "");
  const inputElement = useRef<HTMLInputElement>(null);
  const focusAndScrollToInputElement = () => {
    if (inputElement.current) {
      inputElement.current.focus();
      inputElement.current.scrollIntoView({
        behavior: "smooth",
        block: "end",
      });
    }
  };

  const onSubmit = (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const command = prompt || "";
    if (onOk) {
      onOk(command);
    }
    setPrompt("");
    focusAndScrollToInputElement();
  };

  useEffect(() => {
    focusAndScrollToInputElement();
  }, [prompt]);

  return (
    <form onSubmit={onSubmit}>
      <label>
        <span className="color--green">➜</span>&nbsp;
        <span className="color--light-blue">you@marcotisi.dev</span>&nbsp;
        <span className="color--red">~</span>
        <input type="text"
               ref={inputElement}
               value={prompt}
               autoCapitalize="off"
               autoComplete="off"
               autoCorrect="off"
               spellCheck="false"
               onChange={e => setPrompt(e.currentTarget.value)}
               onBlur={() => inputElement.current && inputElement.current.focus()}
               readOnly={!active}
               disabled={!active}
               autoFocus />
      </label>
    </form>
  );
};
