import React, { FunctionComponent } from "react";
import { HistoryItem } from "./HistoryItem";

export type History = Array<HistoryItem>;

export const History: FunctionComponent<{ history: History }> = ({ history }) => {
  return (
    <ul id="history">
      {history.map(({ output, prompt }, index) => (
        <HistoryItem key={index} prompt={prompt} output={output} />
      ))}
    </ul>
  );
};
